import { Container, Row, Col } from "react-bootstrap";
import logo from "../assets/images/neon_logo-modified.png";
import navIcon1 from "../assets/images/nav-icon1.svg";
import navIcon2 from '../assets/images/HR.svg';

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <Col size={12} sm={6}>
            <img src={logo} alt="Logo" />
          </Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="https://www.linkedin.com/in/shivanshsoni-iris"><img src={navIcon1} alt="Icon" /></a>
              <a href="https://www.hackerrank.com/shivansh_007soni?hr_r=1"><img src={navIcon2} alt="linkedin" /></a>
            </div>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}