import { Container, Row, Col } from "react-bootstrap";
import headerimg from "../assets/images/header-img.svg";
import { useEffect, useState } from "react";

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState("");
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [index, setIndex] = useState(1);
  const toRotate = ["Web Developer", "Web Designer", "3D Artist","Coder"];
  const period = 2000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="align-items-center">
          <Col xs={12} md={6} xl={7}>
            <span className="tagline">Welcome to my Portfolio</span>
            <h1>
              {`Hi I'm Shivansh Soni,`} <span className="wrap">{text}</span>
            </h1>
            <p> Im a senior software engineer with more than 2 years of experience in developing web applications using modern technologies such as Angular, JS, HTML, CSS, Django, Python and Java. Im passionate about creating user-friendly, responsive and scalable websites that meet the needs of the clients and the users. I also have some knowledge of 3D designing and graphics, which I use to enhance the visual appeal of my projects. Im always eager to learn new skills and explore new challenges in the field of web development.</p>
          </Col>
          <Col xs={12} md={6} xl={5}>
            <img src={headerimg} alt="Header Img" />
          </Col>
        </Row>
      </Container>
    </section>
  );
};
