import { Col } from "react-bootstrap";

export const ProjectCard = (props) => {
  return (
    <Col  sm={6} md={4}>
      <a href={props.main} className="nav-link">
      <div className="proj-imgbx">
        <img src={props.imgUrl}  />
        <div className="proj-txtx">
            <h4>{props.title}</h4>
          <span>{props.description}</span>
        </div>
      </div>
      </a>
    </Col>
  )
}