import "react-multi-carousel/lib/styles.css";
import { Container, Row, Col } from "react-bootstrap";
import colorSharp from "../assets/images/color-sharp.png";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
export const Skills = () => {
  const skills = [
    {
      name: "REACT",
      skill: 90,
    },
    {
      name: "JAVA",
      skill: 80,
    },
    {
      name: "PYTHON",
      skill: 85,
    },
    {
      name: "HTML,CSS,JS",
      skill: 85,
    },
    {
      name: "DJANGO",
      skill: 85,
    },
  ];
  return (
    <section className="skill" id="skills">
      <Container>
        <Row>
          <Col>
            <div className="skills-bx">
              <h2>Skills</h2>
              <p>I have experience in following skills.</p>
            </div>
          </Col>
        </Row>
        <Row>
          {skills.map((skill) => {
            return (
              <Col md={3} xs={6} sm={6} lg={2}>
                <div className="item">
                  <div className="metering">
                    <CircularProgressbar
                      value={skill.skill}
                      text={skill.skill}
                      className="meterbase"
                    />
                  </div>
                  <br />
                  <h6>{skill.name}</h6>
                </div>
              </Col>
            );
          })}
        </Row>
      </Container>
      <img src={colorSharp} className="background-image-left" alt="" />
    </section>
  );
};
