import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import "animate.css";
import TrackVisibility from "react-on-screen";
import todo from "../assets/images/project-img1.png";

export const Projects = () => {
  const projects = [
    {
      title: "Test React App",
      description: "A basic Todo app to add and remove todos",
      imgUrl: todo ,
      mainUrl: "https://shivansh-soni-iris.github.io/TodoApp/"
    },
  ];
  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>Projects</h2>
                  <p>
                    Below are the projects created by me, currently there is
                    only one project just for testing.
                  </p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav
                      variant="pills"
                      className="nav-pills mb-5 justify-content-center align-items-center"
                      id="pills-tab"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">Projects</Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="second">Certificates</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                title={project.title}
                                description={project.description}
                                imgUrl={project.imgUrl}
                                main={project.mainUrl}
                              />
                            );
                          })}
                        </Row>
                      </Tab.Pane>

                      <Tab.Pane eventKey="second">
                        <ol className="certificates">
                          <li>
                            <a
                              href="https://www.udemy.com/certificate/UC-d2db49f7-7f7f-4502-a702-7b670efbcb20/"
                              className="nav-link"
                            >
                              React Certificate
                            </a>
                          </li>
                          <li>
                            <a
                              href="https://www.udemy.com/certificate/UC-027000a3-f680-4625-a3d2-48b81d066f84/"
                              className="nav-link"
                            >
                              Django Certificate
                            </a>
                          </li>
                          <li>
                            <a
                              href="https://www.hackerrank.com/certificates/2117ef2e633d"
                              className="nav-link"
                            >
                              Problem solving basic (HackerRank)
                            </a>
                          </li>
                        </ol>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  );
};
